import { FieldHookConfig, useField } from "formik";
import * as React from 'react';
import { ComponentPropsWithoutRef } from "react";

interface SelectInputProps extends ComponentPropsWithoutRef<"select"> {
    label: string
}

function SelectInput({ label, ...props }: SelectInputProps & FieldHookConfig<string>) {
    const [field, meta] = useField(props);
    return (
        <div className="block field">
            <label htmlFor={props.id || props.name} className="subtitle is-4 has-text-grey">{label}</label>
            <br />
            <div className="control is-expanded">
                <div className="select is-fullwidth is-info is-medium mt-3 mb-3">
                    <select {...field} {...props as ComponentPropsWithoutRef<"select">} />
                </div>
            </div>
            {meta.touched && meta.error ? (
                <div className="ml-2 mt-1 has-text-danger is-size-7">{meta.error}</div>
            ) : null}
        </div>
    );
}

export { SelectInput };
