import * as React from 'react';
import { ComponentPropsWithoutRef } from 'react';

interface ButtonProps extends ComponentPropsWithoutRef<"button"> {
    isLoading?: boolean
}

function CellButton(props: ButtonProps) {
    let customClassName = props.className;
    const { isLoading, className, ...buttonProps } = props
    if (isLoading) {
        customClassName = customClassName + " is-loading";
    }

    return (
        <button className={customClassName} {...buttonProps}>{props.children}</button>
    );
}

export { CellButton };
