import * as React from 'react';
import Product from '../models/product';

interface ProductsBillInterface {
    prodsSolicitar: Product[]
}

function ProductsBill(props: ProductsBillInterface) {
    console.log(props.prodsSolicitar);
    const usdList: Product[] = [];
    const mxnList: Product[] = [];
    props.prodsSolicitar.forEach(p => {
        const strippedCurrency = p.currency?.trim();
        if (strippedCurrency === "USD") {
            usdList.push(p);
        }
        else if (strippedCurrency === "MXN") {
            mxnList.push(p);
        }
    });

    return (
        <React.Fragment>
            <h5 className="title is-5">Materiales Solicitados ({props.prodsSolicitar.length})</h5>
            <div className='block'>
                {
                    mxnList.length == 0 && usdList.length == 0 &&
                    <table className="table is-striped is-hoverable">
                        <thead>
                            <tr>
                                <th>Item</th>
                                <th>Precio</th>
                                <th>Cantidad</th>
                                <th>Subtotal</th>
                            </tr>
                        </thead>
                        <tfoot>
                            <tr>
                                <th>Total</th>
                                <th></th>
                                <th></th>
                                <th>${0.0.toFixed(2)}</th>
                            </tr>
                        </tfoot>
                    </table>
                }
                {
                    mxnList.length > 0 &&
                    <table className="table is-striped is-hoverable">
                        <thead>
                            <tr>
                                <th>Item</th>
                                <th>Precio</th>
                                <th>Cantidad</th>
                                <th>Subtotal</th>
                            </tr>
                        </thead>
                        <tfoot>
                            <tr>
                                <th>Total</th>
                                <th></th>
                                <th></th>
                                <th>${mxnList.reduce((acc, p) => acc + parseFloat(p.precioT), 0).toFixed(2)} MXN</th>
                            </tr>
                        </tfoot>
                        <tbody>
                            {mxnList.map(p => {
                                return (
                                    <tr key={p.codigo}>
                                        <td>{p.comentarios}</td>
                                        <td>${p.precioU} MXN</td>
                                        <td>{p.cantidad}</td>
                                        <td>${parseFloat(p.precioT).toFixed(2)} MXN</td>
                                    </tr>
                                );
                            })}
                        </tbody>
                    </table>
                }
                {
                    usdList.length > 0 &&
                    <table className="table is-striped is-hoverable">
                        <thead>
                            <tr>
                                <th>Item</th>
                                <th>Precio</th>
                                <th>Cantidad</th>
                                <th>Subtotal</th>
                            </tr>
                        </thead>
                        <tfoot>
                            <tr>
                                <th>Total</th>
                                <th></th>
                                <th></th>
                                <th>${usdList.reduce((acc, p) => acc + parseFloat(p.precioT), 0).toFixed(2)} USD</th>
                            </tr>
                        </tfoot>
                        <tbody>
                            {usdList.map(p => {
                                return (
                                    <tr key={p.codigo}>
                                        <td>{p.comentarios}</td>
                                        <td>${p.precioU} USD</td>
                                        <td>{p.cantidad}</td>
                                        <td>${parseFloat(p.precioT).toFixed(2)} USD</td>
                                    </tr>
                                );
                            })}
                        </tbody>
                    </table>
                }
            </div>
        </React.Fragment>
    );
}

export { ProductsBill };
